import uniList from "../../components/uni-list/uni-list.vue"
import uniListItem from "../../components/uni-list-item/uni-list-item.vue"
export default {
	components: {uniList,uniListItem},
	data() {
		return {
			address:'',
			mobile:'',
			name:'',
			USERINFO:[],
			latitude:'',
			longitude:''
			
		}
	},
	onLoad:function(Option){
		this.USERINFO = this.checkLogin();//判断用户是否登陆
		this.initAddress();//初始化地址信息
		this.doLocation();

	},
	onBackPress:function(Options){
		
	},
	methods: {
		formSubmit:function(e){
			console.log(e.detail.value.user_id = uni.getStorageSync('USERID'));
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value;
			console.log(formdata);
			uni.request({
				url:"https://dongyi.sir6.cn/api/address/save",
				method:'POST',
				data:JSON.stringify(formdata),
				success: (res) => {
					console.log(res);
					if(res.data.err==0){
						uni.showModal({
							title:'提示',
							content:'地址变更成功',
							showCancel:false
						})
					}else{
						uni.showModal({
							title:'提示',
							content:'地址变更失败',
							showCancel:false
						})
					}
				}
			})
			uni.navigateBack({
			    delta: 1,
			});
		},
		initAddress:function(){
			uni.request({
				url: this.apiServer+'/api/address/index?user_id='+uni.getStorageSync('USERID'),
				method: 'GET',
				success: res => {
					var data = res.data;
					console.log(data.err);
					if(data.err==1){
						uni.showModal({
							title:'错误提示',
							content:'请完善您的服务地址',
							showCancel:false
							
						});
					}else{
						this.address = data.data.address;
						this.name = data.data.name;
						this.mobile = data.data.mobile;
					}
				},
			});
		},
		
		doLocation:function(){
			var tt = this
			uni.chooseLocation({
			    success: function (res) {
			        console.log(res)
					tt.address = res.address + res.name;
					tt.latitude = res.latitude;
					tt.longitude = res.longitude;
			    }
			});
		}
	}
}
